.testimonial-container .testimonial {
  padding: 15px;
  margin: auto; }
  @media screen and (min-width: 992px) {
    .testimonial-container .testimonial {
      max-width: 960px; } }
  .testimonial-container .testimonial .header-container {
    margin: auto; }
    @media screen and (min-width: 992px) {
      .testimonial-container .testimonial .header-container {
        display: flex;
        justify-content: center;
        transform: translateX(-5%); } }
    .testimonial-container .testimonial .header-container .header p {
      font-weight: bold;
      font-size: 10px;
      text-align: center;
      color: #6480EB; }
      @media screen and (min-width: 992px) {
        .testimonial-container .testimonial .header-container .header p {
          font-size: 20px; } }
    .testimonial-container .testimonial .header-container .header h4 {
      font-family: "After-Night", sans-serif;
      font-size: 36px;
      text-align: center;
      color: #2C3652; }
      @media screen and (min-width: 992px) {
        .testimonial-container .testimonial .header-container .header h4 {
          font-size: 48px; } }
  @media screen and (max-width: 991px) {
    .testimonial-container .testimonial .testimonial-item-container {
      flex: none;
      max-width: none;
      margin-bottom: 30px; } }
  .testimonial-container .testimonial .testimonial-item-container .testimonial-item {
    margin: auto;
    max-width: 300px;
    min-height: 300px;
    height: 100%;
    padding: 20px 25px;
    background: white;
    box-shadow: 0px 30px 20px rgba(0, 0, 0, 0.03);
    border-radius: 20px;
    display: flex;
    flex-direction: column; }
    @media screen and (max-width: 370px) {
      .testimonial-container .testimonial .testimonial-item-container .testimonial-item {
        min-height: 520px; } }
    @media screen and (max-width: 991px) {
      .testimonial-container .testimonial .testimonial-item-container .testimonial-item {
        min-height: 350px; } }
    .testimonial-container .testimonial .testimonial-item-container .testimonial-item .desc {
      font-weight: 600;
      font-size: 14px;
      color: #6E7F95;
      flex: 1; }
    .testimonial-container .testimonial .testimonial-item-container .testimonial-item .title {
      font-weight: bold;
      font-size: 18px;
      color: #2C3652; }
    .testimonial-container .testimonial .testimonial-item-container .testimonial-item .sub-title {
      font-weight: bold;
      font-size: 14px;
      color: #6480EB; }
  .testimonial-container .testimonial .comma-up {
    margin-right: 10px; }
  .testimonial-container .testimonial .comma-down {
    transform: translateY(-35px) translateX(-30px) rotate(180deg); }
    @media screen and (max-width: 991px) {
      .testimonial-container .testimonial .comma-down {
        transform: translateY(-25px) translateX(-30px) rotate(180deg); } }
  @media screen and (max-width: 991px) {
    .testimonial-container .testimonial .comma-up img,
    .testimonial-container .testimonial .comma-down img {
      height: auto;
      width: 50px; } }
