      .business-main-container .carousel-container .insurer-logo-carousel .logo img {
        margin: auto; }

.business-main-container .text-link {
  margin-left: 3px;
  font-size: 14px;
  font-weight: bold;
  color: #00D7AF; }
  .business-main-container .text-link:hover {
    text-decoration: underline; }

.business-main-container .slider-container .slick-slider {
