.landing-testimonial-container {
  margin: 50px 0;

  .sponsor-container {
    max-width: 220px;
    margin: 100px auto 0 auto;

    @media screen and (min-width: 576px) {
      margin-top: 50px;
      max-width: 500px;
    }

    @media screen and (min-width: 992px) {
      margin-top: 100px;
      max-width: 1148px;
    }

    .sponsor-img {
      margin: 10px 0;
      max-width: 100%;
      padding: 0 10px;
      display: flex;
      justify-content: center;

      img {
        object-fit: contain;
        max-width: 150px;

        @media screen and (max-width: 991px) {
          max-width: 100px;
        }
  
        width: 100%;
        height: 81px;
        filter: saturate(0);
      }
    }
  }

  .testimonial-container {
    .react-slick-carousel {
      width: calc(100% + 30px);
      transform: translateX(-15px);
    }
  }
}
